.header-banner{
    margin-bottom: 20px;
    background-image: linear-gradient(
        66.99deg, 
        rgba(0, 0, 0, 0.4) 0%, 
        rgba(68, 68, 68, 0.29) 48.36%, 
        rgba(255, 255, 255, 0) 121.27%), 
        url("../../assets/img/ron-otsu-62_2KGyX13E-unsplash.jpg");
        visibility: visible;
        height: 470px;
        width: 100%;
        overflow: hidden;
        background-size: 100% 100%;
}
.sub-header-top {
    /* Learn More About Becoming A Business Partner */

    position: absolute;
    text-align: center;
    top: 224px;

    font-style: normal;
    font-weight: bold;
    font-size: 48px;
    line-height: 59px;
    /* identical to box height */

    color: #FFFFFF;
}
.image-section {
    height: 375px;
    background: linear-gradient(180deg, rgba(0, 0, 0, 0.37) 99.99%, rgba(255, 255, 255, 0) 100%);
    mix-blend-mode: normal;
    border: 1px solid #000000;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25), 0px 4px 4px rgba(0, 0, 0, 0.25);
  }

.header-img {
    margin-top: 13%;
}

.reg-style {
    margin-top: 70px;
    color: white;
    font-style: normal;
    font-weight: 500;
    font-size: 48px;
    line-height: 59px;
    text-align: left;

}

.redirect-style {
    margin-top: 15px;
    color: white;
    font-style: italic;
    font-weight: 100;
    font-size: 36px;
    line-height: 44px;
    text-align: right;
}

.intro-section{
    padding-top: 60px;
    padding-bottom: 80px;
    font-weight: 100;
    font-size: 25px;
    line-height: 34px;
    text-align: center;
}

.how-it-works {
    color: #89A549;
    font-size: 28px;
    font-weight: 600;
    text-align: center;
    padding-bottom: 10px;


}

.h-line {
    border-color: #89A549;
    border-width: 2px;
    width: 85%;
}

.mag-glass {
    width: 220px;
    height: 210px;
    left: 52px;
    top: -10px;
    margin-top: -40px;
}

.discover-text {
    text-align: left;
    font-size: 30px;
    margin-left: 30px;
}

.img-style {
    width: 230px;
    height: 250px;
    overflow: hidden;
    object-fit: cover;
    filter: brightness(70%); 
}

.container {
    border: 7px solid #89A549;
    padding: 0px;
    position: relative;
    text-align: center;
    color: white;
    overflow: hidden;
    mix-blend-mode: normal;
    
}

.overlay-text {
    position: absolute;
    width: 230px;
    font-size:25px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.block-text {
    width: 230px;
    margin: auto;
    margin-top: 10px;
    text-align: center;
}

.cursor {
    width: 190px;
    height: 220px;

    margin-top: -20px;
}

.select-text {
    text-align: right;
    font-size: 30px;
    
}

.share-icon {
    width: 180px;
    height: 180px;
    left: 52px;
    top: -10px;
    margin-top: -50px;
}

.share-text {
    text-align: left;
    font-size: 30px;
    margin-left: 30px;
    margin-top: -25px;
}

.green-box {
    width: 210px;
    height: 210px;

    margin-top: -20px;
}

.receive-text {
    text-align: right;
    font-size: 30px;
    
}

.reg-detail {
    font-size: 20px;
}

.image-small {
    vertical-align: middle;
    width: 120px;
    height: 120px;
    border: 4px solid #89A549;
    margin-right: 20px;
    overflow: hidden;
    object-fit: cover;
    filter: brightness(90%); 
}

.detail-text{
    vertical-align: text-top;
    width: 550px;
    display: inline-block;
    margin-top:-5px;
}